.main_wrapper {
  display: flex;
  height: 100%;
  min-height: 1000px;
  flex-direction: column;
}
.contactIconsDiv{
  width: 50%; 
}
.contactIcon{
    width: 30px;
    height: 30px;
    margin: 0px 10px 0px 0px;
  }
  @media (max-width: 800px) {
    .contactIconsDiv{
      display: flex;
  
    }
  }
  @media (max-width: 500px) {
    .contactIconsDiv {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-left: auto;
      margin-right: auto;
      width: 150px;
    }
  }